/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Libre Franklin', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  line-height: 1.6;
  color: white;
  background-color: black;
  font-size: 1.5rem;
}

/* General H2 styling */
h2 {
  font-size: 4vw;
  font-weight: bold;
  color: white;
  margin: 0;
  line-height: 1.1;
  letter-spacing: -1.5px;
}

.about-title h2 {
  /* Inherits from general h2 style */
}

.App {
  min-height: 100vh;
}

/* Container */
.container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Navigation */
.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  z-index: 1000;
  padding: 1rem 0;
}

.nav-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem 0 3.1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.nav-submit-container {
  display: flex;
  align-items: center;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
}

.logo-image {
  width: 50px;
  height: auto;
}

.logo-text {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: bold;
}

.logo-wp {
  font-family: 'Times New Roman', serif;
  font-style: italic;
}

.nav-links {
  display: flex;
  gap: 2rem;
  align-items: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.nav-submit {
  text-decoration: underline !important;
  text-underline-offset: 4px !important;
  color: white !important;
  font-weight: bold !important;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  font-family: 'IBM Plex Mono', monospace;
  transition: opacity 0.3s ease;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
}

.nav-links a:hover {
  opacity: 0.7;
}

.nav-link.active {
  opacity: 1;
}

.active-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  display: block;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.active-dot.visible {
  opacity: 1;
}

.about-dot {
  background-color: #FF6B6B; /* Bright red */
}

.works-dot {
  background-color: #4ECDC4; /* Bright teal */
}

.join-dot {
  background-color: #45B7D1; /* Bright blue */
}

.faq-dot {
  background-color: #96CEB4; /* Bright green */
}

.nav-submit {
  text-decoration: underline !important;
}

/* Hero Section */
.hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding-top: 80px; /* Account for fixed nav */
}

.hero-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.hero-content {
  max-width: 500px;
}

.hero-headline {
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  color: white;
}

.hero-subhead {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 2.5rem;
  color: white;
  opacity: 0.9;
}

.hero-cta {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.cta-primary {
  background-color: white;
  color: black;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-primary:hover {
  background-color: #f0f0f0;
  transform: translateY(-2px);
}

.cta-secondary {
  background-color: transparent;
  color: white;
  border: 1px solid white;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-secondary:hover {
  background-color: white;
  color: black;
}

/* Story Grid */
.hero-visual {
  display: flex;
  justify-content: flex-end;
}

.story-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 1rem;
  max-width: 400px;
}

.story-card {
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.story-image {
  height: 120px;
  width: 100%;
}

.story-text {
  padding: 1rem;
  color: black;
  font-size: 0.85rem;
  line-height: 1.4;
  font-weight: 500;
}

/* Placeholder images with different colors */
.placeholder-green {
  background: linear-gradient(45deg, #4CAF50, #66BB6A);
}

.placeholder-feet {
  background: linear-gradient(45deg, #FF9800, #FFB74D);
}

.placeholder-coins {
  background: linear-gradient(45deg, #FFD700, #FFEB3B);
}

.placeholder-politics {
  background: linear-gradient(45deg, #9C27B0, #BA68C8);
}

.placeholder-laptop {
  background: linear-gradient(45deg, #2196F3, #64B5F6);
}

.placeholder-laundry {
  background: linear-gradient(45deg, #E91E63, #F06292);
}

/* Placeholder sections */
.section-placeholder {
  padding: 4rem 0;
  background-color: #111;
  min-height: 50vh;
  display: flex;
  align-items: center;
}

.section-placeholder h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: white;
}

.section-placeholder p {
  font-size: 1.1rem;
  color: #ccc;
}

/* Footer */
.main-footer {
  background-color: #111;
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2001;
}

.footer-placeholder p {
  color: #999;
  font-size: 1rem;
  font-family: 'IBM Plex Mono', monospace;
  text-align: center;
}

.main-footer p {
  color: #999;
  font-size: 0.9rem;
}

.left-border-rule {
  position: fixed;
  top: 0;
  left: 2.5%;
  width: 1px;
  height: 100vh;
  background: white;
  z-index: 2000;
  margin-top: 1.5rem;
}

.main-content {
  padding-left: 1.5rem;
  background-color: black;
}

/* About Section */
.about-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 4rem 0;
}

.about-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: start;
}

.about-title h2 {
  /* Inherits from general h2 style */
}

.section-h2 {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  margin: 0;
  line-height: 1.4;
}

.about-content p {
  font-size: 1.5rem;
  line-height: 1.7;
  color: white;
  margin: 0;
  opacity: 0.9;
}

/* How It Works Section */
.how-it-works-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 4rem 0;
}

.how-it-works-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: start;
}

.how-it-works-title h2 {
  /* Inherits from general h2 style */
}

.how-it-works-content p {
  margin-bottom: 2rem;
}

/* Accordion Styles */
.accordion {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.accordion-item {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.accordion-item:first-child {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.accordion-header {
  width: 100%;
  padding: 2rem 0;
  background: none;
  border: none;
  color: white;
  font-size: 1.25rem;
  font-weight: 600;
  font-family: 'IBM Plex Mono', monospace;
  text-align: left;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: background-color 0.3s ease;
}

.accordion-header:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.accordion-icon {
  font-size: 1.5rem;
  font-weight: 300;
  transition: transform 0.3s ease;
}

.accordion-icon.expanded {
  transform: rotate(45deg);
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.accordion-content.expanded {
  max-height: 500px;
  background-color: rgba(255, 255, 255, 0.08);
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 3rem;
}

.accordion-content > div,
.accordion-content > p,
.accordion-content > ul {
  margin: 0;
  font-size: 1.25rem;
}

.accordion-content > div {
  margin-bottom: 1rem;
}

.accordion-content > div:last-child {
  margin-bottom: 0;
}

.accordion-content ul {
  list-style: none;
  padding-left: 0;
}

.accordion-content li {
  margin-bottom: 0.5rem;
  padding-left: 1rem;
  position: relative;
}

.accordion-content li:before {
  content: "•";
  position: absolute;
  left: 0;
  color: #3bf6c566;
}

.offer-item,
.structure-item {
  padding-left: 1rem;
  position: relative;
}

.offer-item:last-child,
.structure-item:last-child {
  margin-bottom: 0;
}

.structure-item {
  margin-bottom: 0;
}

.offer-item:before,
.structure-item:before {
  content: "•";
  position: absolute;
  left: 0;
  color: #3bf6c566;
  font-size: 1.5rem;
  line-height: 1;
}

.offer-item h4,
.structure-item h4 {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  margin: 0 0 0.5rem 0;
  font-family: 'IBM Plex Mono', monospace;
}

.offer-item p,
.structure-item p {
  font-size: 1.25rem;
  line-height: 1.6;
  color: white;
  opacity: 0.5;
  margin: 0;
}

/* Why Join Us Section */
.why-join-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 4rem 0;
}

.why-join-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: start;
}

.why-join-title h2 {
  /* Inherits from general h2 style */
}

.why-join-content p {
  margin-bottom: 1.5rem;
}

.why-join-content h3 {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  margin: 3rem 0 2rem 0;
}

/* Features Grid */
.features-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.feature-card {
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.feature-card:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.feature-card h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: white;
  margin: 0 0 1rem 0;
  font-family: 'IBM Plex Mono', monospace;
}

.feature-card p {
  font-size: 1.25rem;
  line-height: 1.6;
  color: white;
  opacity: 0.45;
  margin: 0;
}

/* FAQ Section */
.faq-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 4rem 0;
}

.faq-container {
  width: 95%;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: start;
}

.faq-title h2 {
  /* Inherits from general h2 style */
}

/* Mobile Menu Styles */
.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  gap: 4px;
}

.hamburger-line {
  width: 24px;
  height: 2px;
  background-color: white;
  transition: all 0.3s ease;
}

.hamburger-line.open:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.hamburger-line.open:nth-child(2) {
  transform: rotate(-45deg) translate(6px, -6px);
}

.mobile-menu {
  position: fixed;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: calc(100vh - 80px);
  background-color: black;
  z-index: 999;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  padding: 2rem;
}

.mobile-menu.open {
  opacity: 1;
  visibility: visible;
}

.mobile-menu-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: left;
  width: 100%;
}

.mobile-nav-link {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 600;
  font-family: 'IBM Plex Mono', monospace;
  transition: opacity 0.3s ease;
}

.mobile-nav-link:hover,
.mobile-nav-link.active {
  opacity: 0.7;
}

.mobile-nav-link.submit {
  text-decoration: underline;
  font-weight: bold;
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-container {
    flex-direction: row;
    gap: 0;
    padding: 0 2rem 0 0;
  }
  
  .desktop-nav {
    display: none;
  }
  
  .nav-submit-container {
    display: none;
  }
  
  .mobile-menu-toggle {
    display: flex;
  }
  
  /* Section Layout Changes */
  .hero-container,
  .about-container,
  .how-it-works-container,
  .why-join-container,
  .faq-container {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: left;
    width: 100%;
    padding: 0 1rem;
  }
  
  .hero-container {
    padding-top: 40vw;
  }
  
  .hero-headline {
    font-size: 2.5rem;
  }
  
  .hero-cta {
    justify-content: flex-start;
  }
  
  /* Mobile Typography */
  h2 {
    font-size: 2rem !important;
  }
  
  body {
    font-size: 1.2rem;
  }
  
  .hero-visual {
    display: flex;
    justify-content: flex-start;
    margin-top: 1.5rem;
  }
  
  .story-grid {
    margin: 0;
  }
  
  .story-card {
    width: 200px;
  }
  
  .story-image {
    height: 200px;
  }
  
  /* Features Grid */
  .features-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .feature-card {
    padding: 1.5rem;
  }
  
  /* Accordion */
  .accordion-header {
    padding: 1.5rem 0;
    font-size: 1rem;
  }
  
  .accordion-content.expanded {
    padding: 1.5rem;
  }
  
  .main-content {
    padding-left: 0;
  }
  
  .left-border-rule {
    display: none;
  }
}

@media (max-width: 480px) {
  .container,
  .nav-container,
  .hero-container {
    padding: 0 1rem;
  }
  
  .hero-headline {
    font-size: 2rem;
  }
  
  .hero-subhead {
    font-size: 1rem;
  }
  
  .cta-primary,
  .cta-secondary {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }
}

.text-highlight {
  background-color: #3bf6c566;
  padding: 0.1rem 0.3rem;
  border-radius: 3px;
}
